<script setup lang="ts">
export interface IdentityBlockProps {
  /**
   * URL of the banner image.
   */
  bannerUrl?: string;
  /**
   * Array of banners.
   */
  banners?: IModelPoster[];
  /**
   * URL of the logo image.
   */
  logoUrl?: string;
  /**
   * Title of the identity block.
   */
  title: string;
  /**
   * Total number of events.
   */
  totalEventsNumber?: number;
  /**
   *  Number of live events.
   */
  liveEventsNumber?: number;
  /**
   * Number of upcoming events.
   */
  upcomingEventsNumber?: number;
  /**
   * Indicates whether displaying amount of events or not
   */
  hasEventsNumber?: boolean;
  /**
   * Indicates date of identity (basically it used for league structure in format 2023/2024)
   */
  date?: string;
  /**
   * Indicates whether the user is following.
   */
  isFollowing?: boolean;
  /**
   * Indicates whether following is disabled.
   */
  isFollowingDisabled?: boolean;
  /**
   * Indicates whether the following button is displayed.
   */
  hasFollowingButton?: boolean;
  /**
   * URL of the Facebook profile.
   */
  facebookUrl?: string;
  /**
   * URL of the X profile.
   */
  xUrl?: string;
  /**
   * URL of the Instagram profile.
   */
  instagramUrl?: string;
  /**
   * URL of the website.
   */
  webUrl?: string;
  /**
   * URL of the YouTube profile.
   */
  youtubeUrl?: string;
}

const { t } = useI18n();
const color = useColorMode();
const props = withDefaults(defineProps<IdentityBlockProps>(), {
  totalEventsNumber: 0,
  liveEventsNumber: 0,
  upcomingEventsNumber: 0,
});
const isMobile = useIsMobile();
const emit = defineEmits(["follow"]);
const onFollowClick = () => emit("follow");
const isDark = computed(() => color.value === "dark");
const withSocialIcons = computed(() => props.facebookUrl || props.xUrl || props.webUrl || props.instagramUrl || props.youtubeUrl);
</script>

<template>
  <div class="flex flex-col">
    <div v-if="props.banners?.length" class="w-full h-full aspect-[2/1] md:aspect-[4/1] lg:aspect-[25/6] overflow-hidden">
      <BaseSlider
        :slides="props.banners"
        has-autoplay
        has-controls
        has-indicator
      />
    </div>

    <div class="flex px-4 sm:px-10 py-6 w-full gap-6 bg-[var(--base-card-bgColor)]">
      <div class="w-full flex justify-between items-center gap-4" :class="[!props.banners?.length ? 'flex-col pt-10' : 'flex-row']">
        <div
          class="flex gap-4 items-center"
          :class="[!props.banners?.length ? 'flex-col' : 'flex-row']"
        >
          <div>
            <BaseLogo
              class="relative z-10"
              :class="{ border: !isDark }"
              :src="props.logoUrl"
              :alt="props.title"
              :width="isMobile ? 72 : 120"
              :height="isMobile ? 72 : 120"
              no-background
              fit="contain"
              :icon-background="isDark ? '#2c374b' : '#ffffff'"
              with-initials
              :title="props.title"
            />
          </div>
          <div class="text-main font-bold line-clamp-2" :class="[!props.banners?.length ? 'text-2xl text-center' : 'sm:text-xl md:text-3xl']">
            <p>
              {{ props.title }}
            </p>

            <div
              v-if="props.hasEventsNumber"
              class="hidden mt-4 md:flex items-center justify-center text-sm text-secondary text-sm font-medium rounded"
            >
              <div class="flex text-start min-w-[5rem]">
                <span>{{ props.liveEventsNumber }} {{ t("labels.live") }}</span>
              </div>

              <div class="flex gap-2 text-center border-x px-4 border-[var(--base-header-menu-line-color)] min-w-[5rem]">
                <p>{{ props.upcomingEventsNumber }}</p> <p>{{ t("labels.upcoming") }}</p>
              </div>

              <div class="flex justify-end px-2 min-w-[5rem]">
                <span>{{ props.totalEventsNumber }} {{ t("labels.total") }}</span>
              </div>
            </div>
          </div>
        </div>

        <div class="flex flex-wrap gap-2 md:gap-4 justify-end items-center" :class="[!props.banners?.length ? 'flex-col py-3' : 'flex-row']">
          <div class="hidden md:flex text-[var(--base-icon-color)]">
            <BaseSocialIcons
              v-if="withSocialIcons"
              :facebook-url="props.facebookUrl"
              :x-url="props.xUrl"
              :instagram-url="props.instagramUrl"
              :youtube-url="props.youtubeUrl"
              :web-url="props.webUrl"
            />
          </div>
          <BaseButton
            v-if="props.hasFollowingButton"
            class="text-button-bold"
            :class="[!props.banners?.length ? 'w-[8rem]' : 'md:w-[12rem]']"
            :size="isMobile ? 'small' : 'medium'"
            :type="props.isFollowing ? 'secondary' : 'primary'"
            name="follow-club"
            :disabled="props.isFollowingDisabled"
            @click.prevent="onFollowClick()"
          >
            {{ props.isFollowing ? t("labels.following") : t("labels.follow") }}
          </BaseButton>
        </div>
      </div>
    </div>

    <div class="block md:hidden">
      <div v-if="props.hasEventsNumber" class="bg-[var(--base-card-bgColor)] py-3 text-secondary text-sm border-y border-[var(--base-header-menu-line-color)]">
        <div class="flex font-medium rounded">
          <div class="flex-1 text-center px-2">
            {{ props.liveEventsNumber }} {{ t("labels.live") }}
          </div>
          <div class="flex-1 border-x px-4 border-[var(--base-header-menu-line-color)] text-center px-2 ">
            <span>{{ props.upcomingEventsNumber }} {{ t("labels.upcoming") }}</span>
          </div>
          <div class="flex-1 text-center px-2 ">
            <span>{{ props.totalEventsNumber }} {{ t("labels.total") }}</span>
          </div>
        </div>
      </div>

      <div
        v-if="withSocialIcons"
        class="py-3 w-full text-[var(--base-icon-color)] flex items-center justify-center bg-[var(--base-card-bgColor)]"
      >
        <BaseSocialIcons
          :facebook-url="props.facebookUrl"
          :x-url="props.xUrl"
          :instagram-url="props.instagramUrl"
          :youtube-url="props.youtubeUrl"
          :web-url="props.webUrl"
        />
      </div>
    </div>
  </div>
</template>
